.modal {
  width: 100%;
  background-color: #fffdee;
  border-radius: 5px;
  padding: 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
